* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgba(218, 224, 239, 0.29);
}

#root {

  display: flex;
  justify-content: center;
}

.App {

  width: 800px;
}

header {
  display: flex;
  padding: 15px;
  margin-top: 15px;
  justify-content: space-between;
  align-items: center;
  font-size: 32px;
  color: #000000;
  font-family: Georgia, serif;
}


